@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  .spinner-container{
    margin-top: 0px;
    display: flex;
    justify-content: center;
  }
  .loading-spinner {
    width: 20px;
    height: 20px;
    border: 4px solid #ff0000; /* Light grey */
    border-top: 4px solid #383636; /* Black */
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
  }

  div{
    display: flex;
    flex-direction: column;
  }
  p{
    font-size: larger;
    color: black;
  }
  video{
    height: 80%;
    width:60%;
   
  }